<template>
  <div class="cpi-container-header">
    <div>
      <Icon type="ios-man" /> {{userTel}}
      <span class="ml-10">欢迎登录管理系统！</span>
    </div>
    <div>星期<span class="mr-10">{{week}}</span>{{nowTime}}</div>
  </div>
</template>
<script>
import { mapGetters } from 'vuex'
import { getWeek, dateFormate } from '@/utils/dateFormate.js'
export default {
  computed: {
    ...mapGetters(['userTel']),
    week () {
      return getWeek()
    },
    dateStr () {
      return dateFormate(new Date(), 'yyyy-MM-dd hh:mm')
    }
  },
  data () {
    return {
      nowTime: ''
    }
  },
  mounted () {
    let slef = this
    setInterval(function () {
      slef.nowTime = dateFormate(new Date(), 'yyyy-MM-dd hh:mm')
    }, 1000)
  }
}
</script>
<style lang="less" scoped>
.cpi-container-header {
  height: 50px;
  background-color: #fff;
  box-shadow: 0px 2.5px 2.5px 0 rgba(0, 0, 0, 0.1);
  position: fixed;
  top: 50px;
  left: 240px;
  right: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 74px 0 41px;
  color: #8b8b8b;
}
</style>